import RegisterFormContainer from "../Components/Templates/RegisterFormContainer/RegisterFormContainer";
import { validSignIn } from "./validation";
export var submit = function (e) {
    e.preventDefault();
    var email = document.getElementsByName("email")[0];
    var userName = document.getElementsByName('userName')[0];
    var psw = document.getElementsByName("psw")[0];
    if (validSignIn(email.value, psw.value)) {
        alert("Hi ".concat(userName.value, ", Nice to see you again!"));
        return true;
    }
};
export var registerForm = function (e) {
    e.preventDefault();
    var app = document.getElementsByClassName('app')[0];
    var form = document.getElementsByClassName('formContainer-login')[0];
    form.remove();
    var newForm = new RegisterFormContainer().RegisterFormContainer;
    app.appendChild(newForm);
};
export var loginValidation = function () {
    var email = document.getElementsByName('email')[0];
    var psw = document.getElementsByName('psw')[0];
    validSignIn(email.value, psw.value);
};
